import React from 'react';
import ReactDOM from 'react-dom';
import {hashHistory} from 'react-router'


import './../scss/user.scss';

export default class User extends React.Component{
	constructor(props){
		super(props)
	}
	tohomeFn(){
		localStorage.removeItem('userID');
		hashHistory.push('/')
	}
	
	componentDidMount(){
		$('#header').css("display","none");
		console.log(localStorage.getItem('userID'))
		$('.usernamemsg  .username').html(localStorage.getItem('userID'))
	}
	render(){
		return(
				<div id = 'container'>
					<div id = 'userheader'>
						<div className = 'userheadertop'>
							<b className = 'iconfont setitems'>&#xe618;</b>
							<b className = 'iconfont'>&#xe647;</b>
						</div>
						<div className = 'usermsg'>
							<div className = 'touxiang'>
								<img src = './../img/touxiang.jpg' />
							</div>
							<div className = 'usernamemsg'>
								<p className = 'username'></p>
								<p className = 'address'><b className = 'iconfont'>&#xe610;</b> </p>
							</div>
							<b className = 'iconfont'>&#xe610;</b>
						</div>
						<div className = 'sleffocus'>
							<div className = 'sleffocusitem'>
								<p><b className = 'iconfont'>&#xe621;</b></p>
							</div>
							<div className = 'sleffocusitem'>
								<p><b className = 'iconfont'>&#xe60f;</b></p>
							</div>
							<div className = 'sleffocusitem'>
								<p><b className = 'iconfont'>&#xe603;</b></p>
							</div>
						</div>
					</div>
					<div id = 'usercontent'>
						<ul className = 'top'>
							<li className = 'usermsg topli'><b className = 'iconfont'>&#xe604;</b><b className = 'iconfont right'>&#xe610;</b></li>
							<ul className = 'afterbuy'>
								<li><b className = 'iconfont'>&#xe63f;</b></li>
								<li><b className = 'iconfont'>&#xe739;</b></li>
								<li><b className = 'iconfont'>&#xe606;</b></li>
								<li><b className = 'iconfont'>&#xe654;</b></li>
								<li><b className = 'iconfont'>&#xe605;</b></li>
							</ul>
						</ul>
						<ul className = 'bottom'>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe63c;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe628;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe63f;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe623;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe60f;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe74b;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe662;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
							<li className = 'usermsg'>
								<b className = 'iconfont'>&#xe658;</b><b className = 'iconfont right'>&#xe610;</b>
							</li>
						</ul>
						<button className = 'tuichu' onClick = {this.tohomeFn.bind(this)}></button>
					</div>
				</div>
			)
	}
}


